<template>
  <div class="table-responsive">
    <table v-bind:class="`table ${tbClass}`">
      <thead v-bind:class="headBg">
        <!-- <thead class="thead-light"> -->
        <tr>
          <th
            v-for="(val,idx) in fields"
            v-bind:key="idx"
          >{{fieldsChn.hasOwnProperty(val)?fieldsChn[val]:val}}</th>
        </tr>
      </thead>
      <tbody>
        <!-- <tr v-for="(itm,idx) in items" v-bind:key="idx">
       
        <td v-for="([name,value],m) in Object.entries(itm)" v-bind:key="m">
          {{value}}
        </td>
        </tr>-->

        <tr v-for="(arr,m) in checkedItems" v-bind:key="m">
          <td v-for="(v,n) in arr" v-bind:key="n">{{v}}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  name: "TheTable",
  props: {
    tbClass: {
      type: String,
      default: "table-hover"
    },
    headBg: {
      type: String,
      default: "thead-light"
    },
    fields: {
      type: Array,
      required: true
    },
    fieldsChn: {
      type: Object,
      default: function() {
        return {};
      }
    },
    items: {
      type: Array,
      required: true
    }
  },
  data() {
    return {};
  },
  computed: {
    //items中对象名在fields中出现的才在表格中显示
    checkedItems() {
      const fArr = this.fields;
      const iArr = this.items;
      const arr = [];

      iArr.forEach((obj, idx) => {
        arr[idx] = [];
        for (let name in obj) {
          if (fArr.includes(name)) {
            arr[idx].push(obj[name]);
          }
        }
      });
      return arr;
    }
  },
  methods: {}
};
</script>